import React from 'react'

export default class User extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            topNavSelected: 'shops',
            shoppingList: [
                {description: 'mayo', name: 'mayonaise', brand: 'no name', shopname: 'Pick n Pay', amount: 50}
            ],
            openNavItem: -1,
        };
    }

    componentDidMount() {
        this.test()
    }

	render()	{

        return (
            <div style={{position: 'absolute', height: '100%', width: '100%', background: '#eeeeee', display: 'flex', flexDirection: 'column'}}>
                {/*Top nav bar */}
                <div style={{width: '100%', height: '70px', background: '#FFF96D', boxShadow: '2px 2px 10px #BCBCBC', display: 'flex', alignItems: 'center'}}>
                    <div onClick={() => this.setSelectedNav('shops')} style={{flexGrow: this.state.topNavSelected === 'shops' ? 1 : 0, height: '100%', transition: 'all 0.2s ease', minWidth: '20%', display: 'flex', justifyContent: 'center', alignItems: 'center', cursor: 'pointer', fontSize: '30px', color: "#7B7B7B"}}>
                        Shopping
                    </div>

                    <div style={{height: 'calc(100% - 10px)', width: '2px', background: 'white'}}></div>

                    <div onClick={() => this.setSelectedNav('accounts')} style={{flexGrow: this.state.topNavSelected === 'accounts' ? 1 : 0, minWidth: '20%', transition: 'all 0.2s ease', height: '100%', display: 'flex', justifyContent: 'center', alignItems: 'center', cursor: 'pointer', fontSize: '30px', color: "#7B7B7B"}}>
                        Account
                    </div>

                    <div style={{height: 'calc(100% - 10px)', width: '2px', background: 'white'}}></div>

                    <div onClick={() => this.setSelectedNav('cart')} style={{flexGrow: this.state.topNavSelected === 'cart' ? 1 : 0, minWidth: '20%', height: '100%', transition: 'all 0.2s ease', display: 'flex', justifyContent: 'center', alignItems: 'center', cursor: 'pointer', fontSize: '30px', color: "#7B7B7B"}}>
                        Cart
                    </div>
                    
                </div>

                <div style={{flexGrow: 1, width: '100%', display: 'flex', }}>
                    <div style={{height: '100%', width: '300px', maxWidth: '30%', background: 'white', }}>
                        {this.state.shoppingList.map((prop, key) =>
                            <div onClick={() => this.setOpenNavItem(key)} style={{cursor: 'pointer', height: this.state.openNavItem === key ? '80px' : '60px', width: '100%', background: 'red', transition: 'all 0.2s ease'}}>
                                <div style={{width: '100%', height: '40px'}}></div>
                            </div>
                        )}

                    </div>
                </div>
            </div>
        )
    }

    setOpenNavItem = (openNavItem) => {
        this.setState({openNavItem})
    }

    setSelectedNav = (topNavSelected) => {
        this.setState({topNavSelected})
    }

    test = async () => {
        const response = await fetch(this.props.url+'/api/items');
        console.log(response)
        const data = await response.json();
        console.log(data)
    }
}

