import React from 'react'
import "./app.css"

import Admin from './Admin.js'
import User from './User.js'
export default class App extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
          type: "none",
          loggedIn: false,
          name: "untiled",

          url: 'http://localhost:8080'
        };
    }

	render()	{

        return (
			    <div style={{position: 'absolute', height: '100%', width: '100%', background: '#eeeeee', display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
            {
              !this.state.loggedIn ?
                <div style={{height: '600px', width: '300px', boxShadow: "2px 2px 10px #BCBCBC", display: 'flex', }}>
                  <div onClick={this.openAdmin} className={"button"} style={{flex: 1, height: '100%', display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
                    Admin
                  </div>
                  <div onClick={this.openUser} className={"button"} style={{flex: 1, height: '100%', display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
                    User
                  </div>
                </div>
                :
                this.state.type === "user" ?
                  <User url={this.state.url} />
                    :
                  <Admin name={this.state.name} />
            }
            

          </div>
        )
    }

  openUser = () => {
    this.setState({type: 'user', loggedIn: true})
  }
  openAdmin = () => {
    this.setState({type: 'admin', loggedIn: true})
  }
}

